import {Encodings} from './encodings';
import {Base64} from 'js-base64';

export class Base64Encoder implements Encodings{
  encode(arg: string): string {
    return Base64.encode(arg, false)
  }

  getName(): string {
    return 'Base64 Encoder';
  }
}

export class Base64Decoder implements Encodings{
  encode(arg: string): string {
    return Base64.decode(arg)
  }

  getName(): string {
    return 'Base64 Decoder';
  }
}
