import {Configuration, Encodings} from './encodings';

const hexLengthConfigName = 'length';

export class HexadecimalEncoder implements Encodings {
  config: Configuration<any>[] = [
    new Configuration(hexLengthConfigName, 'number', 4)
  ]

  encode(arg: string): string {
    const hexLength = Configuration.getConfiguration(this.config, hexLengthConfigName, 4)
    const pad = Array(1 + hexLength).join('0')
    let result = '';
    for (let i = 0; i < arg.length; i++) {
      const hex = arg.charCodeAt(i).toString(16);
      result += (pad + hex).slice(-hexLength);
    }

    return result
  }

  getName(): string {
    return 'Hexadecimal Encoder';
  }
}

export class HexadecimalDecoder implements Encodings {
  config: Configuration<any>[] = [
    new Configuration(hexLengthConfigName, 'number', 4)
  ]

  encode(arg: string): string {
    const hexLength = Configuration.getConfiguration(this.config, hexLengthConfigName, 4)
    const regExp = new RegExp(`.{1,${hexLength}}`, 'g')
    const hexes = arg.match(regExp) || [];
    let back = '';
    for (const hex of hexes) {
      back += String.fromCharCode(parseInt(hex, 16));
    }

    return back;
  }

  getName(): string {
    return 'Hexadecimal Decoder';
  }
}
