import {Component, OnInit} from '@angular/core';
import {Base64} from 'js-base64';
import {Encodings} from '../encoding/encodings';
import {Base64Decoder, Base64Encoder} from '../encoding/base64';
import {CdkDragDrop, moveItemInArray} from '@angular/cdk/drag-drop';
import {MatAutocompleteSelectedEvent} from '@angular/material/autocomplete';
import {HexadecimalDecoder, HexadecimalEncoder} from '../encoding/hexadecimal';
import {UrlDecoder, UrlEncoder} from '../encoding/url';

@Component({
  standalone: false,
  selector: 'app-universal',
  templateUrl: './universal.component.html',
  styleUrls: ['./universal.component.scss']
})
export class UniversalComponent implements OnInit {
  out1 = '';
  out2 = '';

  availableEncoders: Encodings[] = [
    new Base64Encoder(), new Base64Decoder(),
    new HexadecimalEncoder(), new HexadecimalDecoder(),
    new UrlEncoder(), new UrlDecoder()
  ]

  availableEncodersValues = this.availableEncoders.map(encoder => encoder.getName())
  availableEncodersMap = this.availableEncoders.reduce((map, encoder) => {
    map[encoder.getName()] = encoder;
    return map;
  }, {});

  selectedEncoders: Encodings[] = []

  constructor() {
  }

  ngOnInit(): void {
  }

  encode() {
    this.out2 = this.selectedEncoders.reduce(
      (previousValue, encoder) => encoder.encode(previousValue),
      this.out1
    )
  }

  decode() {
    this.out2 = Base64.decode(this.out1)
  }

  swap() {
    const temp = this.out1
    this.out1 = this.out2
    this.out2 = temp
  }

  drop(event: CdkDragDrop<Encodings[]>) {
    moveItemInArray(this.selectedEncoders, event.previousIndex, event.currentIndex);
  }

  onEncoderSelected($event: MatAutocompleteSelectedEvent) {
    const encoderName = $event.option.value;
    this.addEncoderNamed(encoderName)
  }

  private addEncoderNamed(encoderName: string) {
    this.selectedEncoders.push(this.availableEncodersMap[encoderName])
  }

  removeSelectedEncoderAt(i: number) {
    this.selectedEncoders.splice(i, 1)
  }
}
