export interface Encodings {
  getName(): string;

  encode(arg: string): string
}

export class Configuration<T> {
  public constructor(public readonly name: string,
                     public readonly type: string,
                     private value: T) {
  }


  static getConfiguration<T>(configurations: Array<Configuration<any>>, name: string, defaultValue: T): T {
    const selectedConfig = configurations.find(value => value.name === name);
    if (selectedConfig !== undefined) {
      return (selectedConfig as Configuration<T>).value
    } else {
      return defaultValue
    }
  }

  static setConfiguration<T>(configurations: Array<Configuration<any>>, name: string, value: T): T {
    const selectedConfig = configurations.find(v => v.name === name)
    if (selectedConfig !== undefined) {
      return (selectedConfig as Configuration<T>).value
    } else {
      return value
    }
  }



  getValue(): T {
    return this.value
  }
}
