import {Encodings} from './encodings';

export class UrlEncoder implements Encodings{
  encode(arg: string): string {
    return encodeURIComponent(arg)
  }

  getName(): string {
    return 'Url Encoder';
  }
}

export class UrlDecoder implements Encodings{
  encode(arg: string): string {
    return decodeURIComponent(arg)
  }

  getName(): string {
    return 'Url Decoder';
  }
}
